<template>
	<div>
		<div class="z_bottom_main">
			<div class="z_bottom_main_1" v-for="i in a_arr">
				<div style="width: 1.1rem;height: 1.1rem;float: left;overflow:hidden;margin-top: 0.1rem;border-radius: 50%;">
					<img style="width: 100%;height: 100%;" :src="i.author.avatar_url"/>
				</div>
				<div style="font-size: 0.16rem;color: black;margin-left: 1rem;margin-top: 0.2rem;">
					<h2>{{i.title}}</h2>
				</div>
				<div style="font-size: 0.16rem;color: black;margin-left: 1rem;margin-top: 0.1rem;">
					<span style="display: block;float: left;">
						{{i.author.loginname}}
					</span>
					<span style="display: block;float: right;">
						{{i.visit_count}}
					</span>
					<!--<div v-for="i in a_arr">
						{{i.content}}
					</div>-->
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				a_arr: [],
			}
		},
		mounted() {
			this.$http.get('https://cnodejs.org/api/v1/topics', {
				params: {
					tab: 'job'
				}
			}).then(data => {
				this.a_arr = data.data.data
//				console.log(this.a_arr);

			});
		},
	}
</script>

<style>
	.z_bottom_main {
		width: 100%;
		height: 1rem;
	}
	
	.z_bottom_main_1 {
		width: 96%;
		min-height: 1rem;
		margin: 0 auto;
		background: white;
		overflow: hidden;
	}
</style>


