<template>
  <div id="app">
    <div class="z_top">
    	<div style="font-size: 0.20rem;color: white;"></div>
    	<div class="z_avatar_url">
    		<img :src="title.data.avatar_url"/>
    	</div>
    	<div class="z_githubUsername">
    		{{title.data.githubUsername}}
    	</div>
    	<div class="z_loginname">
    		{{title.data.loginname}}
    	</div>
    	<div class="z_create_at">
    		<span class="s1">{{title.data.create_at}}</span>
    		<span class="s2">{{title.data.score}}</span>
    	</div>
    </div>
    <div class="z_center">
    	<div class="z_center_1">
    		<a href="#/">ask</a>
				<a href="#/no2">share</a>
				<a href="#/no3">job</a>
				<a href="#/no4">good</a>
    	</div>
    </div>
    <div class="z_bottom">
    	<router-view></router-view>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
 export default {
  name: 'app',
  data() {
   return {
    title: ""
   }
  },
  created() {
   this.convert();
  },
  methods: {
   convert: function() {
    axios.get("https://cnodejs.org/api/v1/user/alsotang").then(res => {
     this.title = res.data;
//     console.log(res.data)
    })
   }
  }
 }
</script>

<style>
*{
	margin: 0;
	padding: 0;
}
.z_top{
	width: 100%;
	min-height: 2.4rem;
	background: #ccc;
	padding-top: 0.2rem;
	padding-bottom: 0.2rem;
}
.z_avatar_url{
	width: 1.1rem;
	height: 1.1rem;
	margin: 0 auto;
	background: red;
	border-radius: 50%;
	overflow: hidden;
}
.z_avatar_url img{
	width: 100%;
	height: 100%;
}

.z_githubUsername{
	text-align: center;
	font-size: 0.22rem;
	color: white;
	margin-top: 0.1rem;
}
.z_loginname{
	text-align: center;
	font-size: 0.20rem;
	color: #F9F9F9;
}
.z_create_at{
	margin-top: 0.3rem;
	font-size: 0.16rem;
}
.s1{
	font-size: 0.16rem;
	color: white;
}
.s2{
	font-size: 0.16rem;
	float: right;
	color: darkgreen;
}
.z_center{
	width: 100%;
	height: 0.6rem;
	background: gray;
}
.z_center_1{
	width: 90%;
	height: 0.6rem;
	background: gray;
	margin: 0 auto;
	display: flex;
	justify-content:space-between;
	font-size: 0.16rem;
	line-height: 0.6rem;
}
.z_center_1 a{
	text-decoration: none;
	color: white;
}
.z_bottom{
	width: 100%;
	min-height: 4rem;
	background: white;
}



</style>



